/*Ajout TBT*/
div.hidden-sub-layers label.chevron {
  display: none; }

div.hidden-sub-layers article.ac-large {
  display: none; }

div.menu-item-checkbox {
  padding-left: 15px; }

div.menu-item-checkbox > label {
  padding-left: 5px; }

div.menu-item-checkbox input[type="checkbox"],
div.category-container input[type="checkbox"] {
  display: none; }

div.menu-item-checkbox > label:before,
div.category-container > label.category-name:before {
  font-family: 'FontAwesome';
  font-style: normal;
  width: 1em;
  height: 1em;
  content: '\f070';
  margin-right: .3em; }

div.menu-item-checkbox input[type="checkbox"]:checked ~ label:before,
div.category-container input[type="checkbox"]:checked ~ label.category-name:before {
  content: '\f06e'; }

div[id^="leaflet-control-accordion-layers"] div.category-container {
  max-height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 5px 10px;
  position: relative;
  z-index: 20;
  color: #3d7489;
  background: #c6e1ec;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#eaeaea',GradientType=0 );
  box-shadow: 0px 0px 0px 1px rgba(155, 155, 155, 0.3), 1px 0px 0px 0px rgba(255, 255, 255, 0.9) inset, 0px 2px 2px rgba(0, 0, 0, 0.1);
  box-sizing: content-box;
  cursor: pointer;
  width: auto;
  margin: 0; }

div.category-container label.category-name {
  flex: 1; }

div.category-container .checked {
  display: none; }

div[id^="leaflet-control-accordion-layers"] input[type="checkbox"]:checked ~ div.category-container label.chevron .checked {
  display: inline-block; }

div[id^="leaflet-control-accordion-layers"] input[type="checkbox"]:checked ~ div.category-container label.chevron .unchecked {
  display: none; }

div[id^="leaflet-control-accordion-layers"] div.category-container label.chevron {
  color: #9fb7c2; }

div[id^="leaflet-control-accordion-layers"] div.category-container label.chevron:hover {
  color: #3d7489; }

/*Style pop-up*/
.leaflet-popup-content-wrapper h2 {
